$black: #000000;
$pink: #f69fa3;
$cream: #ffeddd;

%button-setting {
  color: $black;
  border: 2px solid $black;
  background-color: $cream;
  border-radius: 4px;
  text-transform: uppercase;
  height: 30px;
  outline: none;
}

%button-hover {
  color: $cream;
  border: 2px solid $black;
  background-color: $black;
  border-radius: 4px;
  text-transform: uppercase;
  height: 30px;
  outline: none;
}

@font-face {
  font-family: 'Open Sans', sans-serif;
  src: url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');
  font-weight: normal;
  font-style: normal;
}

.App {
  font-family: 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: normal;
  font-style: normal;
  height: 100vh;
  width: 100%;
  background-color: $cream;
  text-align: center;
}

